export default {

	props: {
		title: {
			type: String,
			default: 'value'
		},
		index:{
			type:Number,
			default:0,
		},
		show: {
			type: Boolean,
			default: true
		},
		radioList: {
			type: Array,
			default: () => ['无']
		},
		tableList: {
			type: Array,
			default: () => [

			]
		},
	},
	computed: {
		selectedRadio() {
			return this.radioList[this.radio];
		}
	},

	data() {
		return {
			modalName: null,
			radio: '1',

		}
	},
	methods: {
		showclick(){
			// this.show = !this.show
			this.$emit('sendChildData',{show:!this.show,index:this.index})
		},
		openPlanDetail() {
			uni.navigateTo({
				url: '/pages/index/detail1'
			});
		},
		showModal(e) {
			console.log(e)
			this.modalName = e.currentTarget.dataset.target
			console.log(this.modalName)
		},
		hideModal(e) {
			this.modalName = null
		},
		RadioChange(e) {
			// 改变选中值
			this.radio = e.detail.value
			// 隐藏选择框
			this.hideModal()

		},
		refresh() {
			console.log("请求api，换一批")
		},
		toDetail() {
			uni.navigateTo({
				url: '/pages/index/detail'
			});

		}
	}
}
